import React, { Component } from 'react'

import AppHeader from '../app-header';
import SearchPanel from '../search-panel';
import PostStatusfilter from '../post-status-filter';
import PostList from '../post-list';
import PostAddForm from '../post-add-form';


import styled from 'styled-components';

const AppBlock = styled.div`
    margin: 0 auto;
    max-width: 800px;
`

export default class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [        
                {label: "React is a cool JavaScript library", important: true, like: false, id: 1},
                {label: "Working with the state could be annoying", important: false, like: false, id: 2},
                {label: "I need a break...", important: false, like: false, id: 3}
            ],
            term: '',
            filter: 'all'
        }
        this.maxId = 4;
        this.deleteItem = (id) => {
            this.setState(({data}) => {
                const index = data.findIndex(elem => elem.id === id);

                const before = data.slice(0, index);
                const after = data.slice(index + 1);

                const newArr = [...before, ...after];

                return {
                    data: newArr
                }
            });
        }
        this.addItem = (body) => {
            const newItem = {
                label: body,
                important: false,
                id: this.maxId++
            }
            this.setState(({data}) => {
                const newArr = [...data, newItem];
                return {
                    data: newArr
                }
            })
        }
        this.onToggleImportant = (id) => {
            this.setState(({data}) => {
                const index = data.findIndex(elem => elem.id === id);
                const old = data[index];
                const newItem = {...old, important: !old.important}

                const newArr = [...data.slice(0, index), newItem, ...data.slice(index + 1)]
                return {
                    data: newArr
                }
            })
        }
        this.onToggleLiked = (id) => {
            this.setState(({data}) => {
                const index = data.findIndex(elem => elem.id === id);
                const old = data[index];
                const newItem = {...old, like: !old.like}

                const newArr = [...data.slice(0, index), newItem, ...data.slice(index + 1)]
                return {
                    data: newArr
                }
            })
        }
        this.onApdateSearch = (term) => {
            this.setState({term})
        }
        this.onfilterSelect = (filter) => {
            this.setState({filter})
        }       
    }

    searchPost(items, term) {
        if (term.length === 0) {
            return items
        }
        
        return items.filter((item) => {
            return item.label.toLowerCase().indexOf(term.toLowerCase()) > -1
        })
    }
    
    filterPost(items, filter) {
        if (filter === 'like') {
            return items.filter(item => item.like)
        } else {
            return items
        }
    }
    

    render() {

        const {data, term, filter} = this.state;
        const liked = data.filter(item => item.like).length;
        const allPosts = data.length;
        const visiblePosts = this.filterPost(this.searchPost(data, term), filter)

        return (
            <AppBlock>
                <AppHeader
                liked = {liked}
                allPosts = {allPosts} />
                <div className="search-panel d-flex">
                    <SearchPanel onApdateSearch={this.onApdateSearch} />
                    <PostStatusfilter
                    filter={filter}
                    onfilterSelect={this.onfilterSelect}/>
                </div>
                <PostList 
                posts={visiblePosts} 
                onDelete = {this.deleteItem}
                onToggleImportant={this.onToggleImportant} 
                onToggleLiked={this.onToggleLiked}/>
                <PostAddForm onAdd={this.addItem}/>
            </AppBlock>
        )
    }
}